import React from "react";
import {Link, useRouteMatch, useLocation} from "react-router-dom";

export default function Sidebar () {

    let {url} = useRouteMatch();
    let linkStyleClass = {
        'paddingBottom': 10,
        'color': 'black',
        'textDecoration': 'none',
        'display': 'block'
    };
    let listStyleClass = {
        'display': 'block'
    };
    let activeListStyleClass = {
        'backgroundColor': '#e2e2e2',
        'borderRadius': '0 50px 50px 0',
        'display': 'block'
    };
    let sideLinks = [
        {
            'path': `${url}/calendar`,
            'displayText': 'Calendar',
            'name': 'calendar'
        },
        {
            'path': `${url}/leave`,
            'displayText': 'Leaves and Overtime',
            'name': 'leave'
        }
    ];

    let currentLocation = useLocation();
    let activeLinkArr = currentLocation.pathname.split("/");
    let activeLink = activeLinkArr[activeLinkArr.length-1];

    return(
        <div className="col-4 col-sm-3">
            <ul className="list-unstyled">
                {sideLinks.map((link, index) => {
                    if(link.name === activeLink){
                        return <li key={index} className="pt-2" style={activeListStyleClass}>
                            <Link className="offset-1" style={linkStyleClass} to={link.path}>{link.displayText}</Link>
                        </li>
                    }
                    return <li key={index} className="pt-2" style={listStyleClass}>
                        <Link className="offset-1" style={linkStyleClass} to={link.path}>{link.displayText}</Link>
                    </li>
                })}
            </ul>
        </div>
    );
}
